﻿html {
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: $color-sec;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: #12405a;
    }

    @include Xs {
        overflow-x: hidden;
    }
    // overflow-x: hidden;

    scrollbar-width: thin;
    scrollbar-color: rgba(#12405a, 1) rgba($color-sec, 1);
}

#ascrail2000 {
    display: none !important;
}

body {
    font-family: "reg", sans-serif;
    overflow: initial !important;

    direction: rtl;
    text-align: right;

    @include Xs {
        overflow-x: hidden !important;
    }

    @include LTR {
        direction: ltr;
        text-align: left;
    }
}

* {
    font-family: inherit;
}

input,
a,
button {
    font-family: "reg", sans-serif;

    &:focus {
        outline: none;
        text-decoration: none;
    }

    &:hover {
        text-decoration: none;
    }
}

button {
    cursor: pointer;

    &:focus,
    &:active {
        outline: none;
        box-shadow: none;
    }
}

ul.res {
    padding: 0;
    margin: 0;
    list-style: none;
}
