@import "main";
// End Of Import

// Loader
.loader {
    @extend .Flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 9999999;

    img {
        width: 250px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(#264f78, 0.95);
    z-index: 9;
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: #264f78;
    z-index: 9999;
    overflow-y: auto;
    transition: 0.5s;
    @extend .Flex;
    flex-direction: column;
    transform-origin: right;
    transform: scaleX(0);

    > img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1.5);
        opacity: 0.3;
    }

    html[dir="ltr"] & {
        transform-origin: left;
    }

    &.open {
        // transform-origin: left;
        transform: scaleX(1);

        ul {
            opacity: 1;

            li {
                transform: translateX(0);
                opacity: 1;
            }
        }
    }

    &.origin {
        transform-origin: left;

        html[dir="ltr"] & {
            transform-origin: right;
        }
    }

    .options {
        padding: 0 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        position: absolute;
        top: 50px;
        direction: ltr;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 14px;
            color: $color-sec;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: #fff;
        }

        .close1 {
            display: flex;
            cursor: pointer;

            img {
                filter: brightness(0) invert(1);
            }
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        opacity: 0;
        transition: 0.5s;
        transition-delay: 0.5s;

        & > li {
            position: relative;
            padding: 10px 0;
            // margin-bottom: 20px;
            transition: 0.5s;
            transform: translateX(10px);
            opacity: 0;

            &:nth-child(1) {
                transition-delay: 0.3s;
            }
            &:nth-child(2) {
                transition-delay: 0.5s;
            }
            &:nth-child(3) {
                transition-delay: 0.7s;
            }
            &:nth-child(4) {
                transition-delay: 0.9s;
            }
            &:nth-child(5) {
                transition-delay: 1.1s;
            }
            &:nth-child(6) {
                transition-delay: 1.3s;
            }

            a {
                width: 100%;
                display: block;
                font-size: 20px;
                color: $color-white;
                transition: 0.5s;
            }

            i {
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: 0.5s;

                html[dir="ltr"] & {
                    left: unset;
                    right: 13px;
                }

                &.open {
                    transform: rotate(0deg);
                }
            }

            .drop {
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li {
                    padding: 4px 0;

                    a {
                        font-size: 15px;
                        color: #898989;
                        transition: 0.5s;

                        &:hover {
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Start Header
header {
    // min-height: 100vh;
    overflow: hidden;
    position: relative;

    #particles-js {
        background-image: linear-gradient(270deg, #12405a 0%, #264f78 100%);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        height: 100vh;
    }

    .TopNav {
        padding-top: 20px;
        padding-bottom: 20px;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        transition: 0.5s;
        z-index: 999;

        &.hide {
            transform: translateY(-100%);
        }

        &.back {
            position: fixed;
            transform: translateY(0);
            background-image: linear-gradient(270deg, #12405a 0%, #264f78 100%);
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
        }

        .content {
            display: flex;
            align-items: center;
        }

        // @include Sm {
        //     justify-content: space-between;
        // }
        .logo {
            height: 70px;
            @include Sm {
                margin-left: auto;
            }
        }

        .links {
            margin-right: 128px;
            margin-left: auto;
            display: flex;
            align-items: center;
            @include LTR {
                margin-left: 128px;
                margin-right: auto;
            }

            @include Sm {
                display: none;
            }

            li {
                &:not(:last-of-type) {
                    margin-left: 25px;
                    @include LTR {
                        margin-right: 25px;
                        margin-left: 0;
                    }
                }
                a {
                    transition: 0.5s;
                    font-size: 18px;
                    font-family: med;
                    color: transparent;
                    // background-image: linear-gradient(to right, #d1a42e 0%, #fff 100%);
                    background-image: linear-gradient(
                        to right,
                        #d1a42e,
                        #d1a42e 50%,
                        #fff 50%
                    );
                    background-size: 200% 100%;
                    background-position: 100%;
                    background-clip: text;
                    -webkit-background-clip: text;

                    &:hover {
                        background-position: 0%;
                    }
                }
            }
        }

        .teckit {
            width: 150px;
            height: 50px;
            background-color: $color-sec;
            border-radius: 100px;
            @extend .Flex;
            transition: 0.5s;
            @extend .hover;

            svg {
                margin-left: 10px;
                @include LTR {
                    margin-right: 10px;
                    margin-left: 0;
                }
            }

            span {
                margin-top: 4px;
                font-size: 18px;
                font-family: med;
                color: #fff;
                text-decoration: none;
            }

            @include mob480 {
                width: 50px;
                height: 50px;

                span {
                    display: none;
                }

                svg {
                    margin: 0;
                }
            }
        }

        .menuTriger {
            display: none;
            justify-content: center;
            align-items: center;
            margin-right: 25px;

            @include Sm {
                display: flex;
            }

            img {
                width: 40px;
                filter: invert(85%) sepia(21%) saturate(1757%)
                    hue-rotate(340deg) brightness(89%) contrast(83%);
            }
        }
    }

    .head {
        padding: 167px 0px 80px;

        .content {
            padding: 20px;
            display: flex;
            align-items: center;
            background-color: #fff;
            border-radius: 0 70px 0 70px;
            box-shadow: 0 3px 36px rgba(0, 0, 0, 0.4);
            position: relative;

            .pic {
                border-radius: 0 75px 0 75px;
                overflow: hidden;
                flex-shrink: 0;
            }

            .details {
                padding: 0 50px 0 45px;
                h2 {
                    font-size: 35px;
                    font-family: bold;
                    color: #244d75;
                    margin-bottom: 20px;

                    + span {
                        @extend .Flex;
                        width: 135px;
                        height: 40px;
                        background-color: $color-sec;
                        border-radius: 34px 0 34px 0;
                        font-size: 16px;
                        font-family: med;
                        color: #fff;
                        padding-top: 5px;
                        margin-bottom: 25px;
                    }
                }

                > p {
                    color: #000000;
                    font-family: med;
                    font-size: 18px;
                    line-height: 30px;
                }

                .actions {
                    display: flex;
                    align-items: center;
                    a {
                        @extend .Flex;
                        @extend .hover;
                        width: 180px;
                        height: 60px;
                        border-radius: 100px;

                        img {
                            margin-left: 5px;
                        }

                        span {
                            font-family: med;
                            font-size: 20px;
                            color: #fff;
                            margin-top: 5px;
                        }

                        &:first-child {
                            background-color: $color-prim;
                            margin-left: 12px;

                            &:after,
                            &:before {
                                background-color: $color-prim;
                            }
                        }

                        &:last-child {
                            background-color: $color-sec;
                        }
                    }
                }

                .download {
                    position: absolute;
                    top: 0;
                    left: 30px;
                    @extend .Flex;
                    width: 284px;
                    height: 60px;
                    background-color: $color-sec;
                    border-radius: 0 40px 0 40px;
                    transform: translateY(-50%);

                    a {
                        span {
                            font-size: 18px;
                            color: #fff;
                            margin-right: 6.5px;
                            font-family: med;
                        }

                        &:first-child {
                            padding-left: 13.5px;
                            margin-left: 17.7px;
                            border-left: 1px solid rgba(#244d75, 0.32);
                        }
                    }
                }
            }
        }
    }
}
// End Header

// Product Details

.productDetails {
    margin-top: 80px;

    span {
        font-size: 22px;
        color: #ababab;
        display: block;
        margin-bottom: 19px;
    }
    h2 {
        font-size: 35px;
        font-family: bold;
        color: #244d75;
        position: relative;

        &:after {
            content: "";
            display: block;
            width: 38px;
            height: 3px;
            background-color: $color-sec;
            border-radius: 4px;
            margin-top: 15px;
            margin-bottom: 33px;

            @include Sm {
                margin-right: auto;
                margin-left: auto;
            }
        }
    }

    p {
        margin-bottom: 0;
        font-size: 18px;
        font-family: med;
        color: #000000;
        line-height: 35px;
    }

    .col-lg-6,
    .col-lg-5 {
        &:not(:last-child) {
            margin-bottom: 80px;
        }
    }
}

// Product Details

