.hover:after, header .TopNav .teckit:after, header .head .content .details .actions a:after, .order-section a:after, .hover:before, header .TopNav .teckit:before, header .head .content .details .actions a:before, .order-section a:before {
  width: 50px;
  height: 50px;
  position: absolute;
  content: "";
  display: inline-block;
  background: #d1a42e;
  border-radius: 50%;
  transition: transform 0.5s ease;
  transform: scale(0);
  z-index: -1;
