@keyframes flash {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 0.5;
  }
  30% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes hang {
  0% {
    transform: translateY(0) translateZ(0);
  }
  25% {
    transform: translateY(-3px) translateZ(0);
  }
  50% {
    transform: translateY(0) translateZ(0);
  }
  75% {
    transform: translateY(3px) translateZ(0);
  }
  100% {
