.Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.hover {
    position: relative;
    filter: url("#goo");

    &:after,
    &:before {
        width: 50px;
        height: 50px;
        position: absolute;
        content: "";
        display: inline-block;
        background: $color-sec;
        border-radius: 50%;
        transition: transform 0.5s ease;
        transform: scale(0);
        z-index: -1;
    }

    &:before {
        top: -25%;
        left: 20%;
    }

    &:after {
        bottom: -25%;
        right: 20%;
    }

    &:hover {
        &:after,
        &:before {
            transform: none;
        }
    }
}

.after {
    position: relative;

    &:after {
        content: "";
        display: block;
        width: 38px;
        height: 3px;
        background-color: $color-sec;
        border-radius: 4px;
        margin-top: 17px;
        margin-bottom: 38px;

        @include Sm {
            margin-right: auto;
            margin-left: auto;
        }
    }
}

@keyframes flash {
    0% {
        opacity: 1;
    }
    10% {
        opacity: 0.5;
    }
    30% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@keyframes hang {
    0% {
        transform: translateY(0) translateZ(0);
    }
    25% {
        transform: translateY(-3px) translateZ(0);
    }
    50% {
        transform: translateY(0) translateZ(0);
    }
    75% {
        transform: translateY(3px) translateZ(0);
    }
    100% {
        transform: translateY(0) translateZ(0);
    }
}

@keyframes ani2 {
    from {
        -webkit-mask-position: 100% 0;
        mask-position: 100% 0;
    }
    to {
        -webkit-mask-position: 0 0;
        mask-position: 0 0;
    }
}

@keyframes ani {
    from {
        -webkit-mask-position: 0 0;
        mask-position: 0 0;
    }
    to {
        -webkit-mask-position: 100% 0;
        mask-position: 100% 0;
    }
}

@keyframes anim {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}
